*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
.contain{
  width: 100vw;
  height:100vh;
  display: grid;
  place-items:center;
}
.contain > h1{
  position: absolute;
  top:5%;
  text-shadow: 2px 2px 2px black;
}
.containImage{
  width:100%;
  height:95vmin;
  
  position: relative;
}

.listImage{
  width:30%;
  height:100%;
  position: absolute;
  bottom:0%;
  
  display:flex;
  flex-direction: column;
  overflow-x:hidden;
  overflow-y:scroll;
  gap:10px;
  scroll-snap-align: center;
  scroll-snap-type:y mandatory;
  transition:all 30ms linear;
}

.listImage > img{
  width:100%;
  height:100%;
  scroll-snap-align: center;
  scroll-snap-type:y mandatory;
  transition:all 300ms linear;
}

.showImage{
  width: 75%;
  height:100%;
  position:absolute;
  display: grid;
  place-items: center;
  right:1%;
}
.showImage > img{
  border-radius:1vmin;
  width:90%;
  height:90%;
  position:absolute;
}

@media only screen and (max-width:550px){
  .contain{
    background:coral;
  }
  .containImage{
    height: 50vmin;
  }
}